#gform-settings .gforms_form_settings .edd-license-key {
  float: left;
  margin: 0 10px 10px 0; }

#gform-settings .gforms_form_settings .gv-edd-button-wrapper {
  float: left;
  padding-left: 0; }
  #gform-settings .gforms_form_settings .gv-edd-button-wrapper .button,
  #gform-settings .gforms_form_settings .gv-edd-button-wrapper .button-primary {
    margin: 0 10px 10px 0 !important; }

/**
 * Modify the GFAddon App Settings page
 */
#tab_gravityview-importer > h3 {
  line-height: 37px; }

#tab_gravityview-importer .gvi-astronaut-head-icon {
  display: inline-block;
  text-indent: -9999px;
  direction: ltr;
  overflow: hidden;
  width: 30px;
  height: 37px;
  float: left;
  margin-right: 10px;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -ie-background-size: cover;
  background-size: cover;
  background-image: url(data:image/svg+xml;base64,); }
  .rtl #tab_gravityview-importer .gvi-astronaut-head-icon {
    float: right;
    margin-left: 10px; }

#tab_gravityview-importer .gv-edd-message {
  min-height: 20px;
  padding: 8px 19px;
  margin: 10px 0 !important;
  border: 1px solid;
  border-radius: 4px;
  position: relative;
  display: block !important;
  background-color: #eeeeee;
  border-color: #cccccc;
  color: #666666; }
  #tab_gravityview-importer .gv-edd-message.valid {
    background-color: #c4ee91;
    border-color: #71af5d;
    color: #4d7615; }
  #tab_gravityview-importer .gv-edd-message.error, #tab_gravityview-importer .gv-edd-message.invalid, #tab_gravityview-importer .gv-edd-message.failed {
    background-color: #fba1a3;
    border-color: #b84f5b;
    color: #981225; }
  #tab_gravityview-importer .gv-edd-message.site_inactive, #tab_gravityview-importer .gv-edd-message.deactivated {
    background-color: #fbeba4;
    border-color: #d7c281;
    color: #958234; }
  #tab_gravityview-importer .gv-edd-message.pending {
    background-color: #d3e4f4;
    border-color: #a9b6c2;
    color: #5c80a1; }
  #tab_gravityview-importer .gv-edd-message p:first-child {
    margin: 0;
    padding: 2px; }

#tab_gravityview-importer .inline.hide:empty {
  display: none !important; }

#tab_gravityview-importer .hide {
  display: none; }
